<template>

  <common-list
    ref="xmcommonlist"
    class="common-list"
    :post-url="list.postUrl"
    :req-data="list.reqData"
    :data-name="list.dataName"
    @returnData="getPrizeLog($event)"
  >
    <div class="container-mini">
      <div class="content">
        <div class="common-main box">

          <!-- <div :class="{'focus':ind === 1}" @click="changeBgc(1)" class="list"> -->
          <div
            v-for="(item, index) in list.items"
            :key="index"
            :class="{ focus: ind === index }"
            class="list"
            @click="itemClick(index)"
          >
            <h3><div class="tit">{{ item.xmmc }}</div> <span class="label">{{ CGFS[item.cgfs] }}</span></h3>
            <div class="info">
              <span>
                <i class="iconfont iconbianma" />
                {{ item.zbbh }}
              </span>
              <span>
                <i class="iconfont iconshouji" />
                {{ item.sj }}
              </span>
              <span>
                <i class="iconfont iconzhuangtai" />
                {{ XMZT[item.xmzt] }}
              </span>
            </div>
            <div class="info">
              <span>
                <i class="iconfont iconshijian" />
                <span v-if="item.kbsj">{{ item.kbsj | formatTime }}</span>
                <span v-else>-</span>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </common-list>

</template>

<script land="script" scoped>

import constants from '@/common/js/constants'
import commonList from '@/components/list/commonList.vue'
import { mapState } from 'vuex'

export default {
  name: 'One',
  components: {
    commonList
  },
  props: {
    xmzt: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      ind: '', // 点击事件
      pageStyles: 0, // 页面状态展示
      pageText: '', // 页面提示信息
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  // computed() {
  //   this.ind = ''
  // },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },

  created() {
    this.ind = ''
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.TimeDay = constants.TimeDay
    // 不会引起DOM变化的数据在此定义
    this.$bus.$on('changeXmQuery', (msg) => { // bus接收事件
      console.log('msg==============', msg)
      const query = {
        xmmc: msg.xmmc,
        cgfs: msg.cgfs
      }
      this.$refs.xmcommonlist.onRefresh(query)
    })
  },
  beforeDestroy() {
    this.$bus.$off('changeXmQuery')
  },
  mounted() {
    this.getdbsxlist()
  },
  methods: {
    getdbsxlist(query) {
      this.list.postUrl = 'jbrwdxm/getjbrWdxmList'
      this.list.reqData = {
        pageno: this.pageno,
        userid: this.userInfo.id,
        xmzt: this.xmzt
      }
      this.list.dataName = 'xmxxList'
      this.list.noDataText = '暂无更多项目信息'
    },
    getPrizeLog(data) {
      this.list.items = data
    },
    changeBgc: function(index) {
      this.ind = index
    },

    itemClick(index) {
      this.$store.commit('jbrwdxm/SET_WDXMITEM', this.list.items[index])
      this.$router.push({ name: 'Xmxxxq' })
    }
  }
}
</script>

<style lang="css" scoped>

</style>
